.grid {
  list-style: none !important;
  margin: 0;
  padding: 0;
  @include medium-up {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
  }
}

.column {
  display: inline-block;
  margin-bottom: ($gutter / 2);
  width: 100%;
  @include medium-up {
    margin: 0 ($gutter / 2) ($gutter / 2) ($gutter / 2);

    &[data-arx-type] {
      margin: 0 0 20px 0;
    }
  }
}

.column-10 {
  @include medium-up {
    width: calc(83% - (#{$gutter} / 1.2));
    margin: 0 ($gutter / 2) ($gutter / 2) ($gutter / 2);

    &:nth-of-type(2n+1) {
      margin-left: 0;
    }

    &:nth-of-type(2n) {
      margin-right: 0;
    }
  }
}

.column-9 {
  @include medium-up {
    width: calc(75% - (#{$gutter} / 1.3333));
    margin: 0 ($gutter / 2) ($gutter / 2) ($gutter / 2);

    &:nth-of-type(2n+1) {
      margin-left: 0;
    }

    &:nth-of-type(2n) {
      margin-right: 0;
    }
  }
}

