}

.column-6 {
  @include medium-up {
    width: calc(50% - (#{$gutter} / 2));

    &:not([data-arx-type]):nth-of-type(2n+1) {
      margin-left: 0;
    }

    &:not([data-arx-type]):nth-of-type(2n) {
      margin-right: 0;
    }
  }
}

